@import url("https://fonts.googleapis.com/css2?family=Inria+Sans:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Baloo+2:wght@400;500;600;700;800&display=swap");
* {
  margin: 0;
  padding: 0;
  border: 0;
  line-height: 1.4em;
  vertical-align: baseline;
  text-decoration: none;
  outline: 0;
  box-sizing: border-box;
  background-color: transparent;
}

html {
  scroll-behavior: smooth;
}

.container {
  max-width: 1600px;
  margin: 0 auto;
  position: relative;
  height: 100%;
}

header {
  position: sticky;
  top: 20px;
  width: calc(100% - 80px);
  margin: 0 40px;
  height: 120px;
  border-radius: 40px;
  z-index: 999;
  background-color: #ffffff;
}
header .header-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
}
header .header-wrapper .logo {
  flex-grow: 1;
  display: flex;
  align-items: center;
}
header .header-wrapper .logo img {
  height: 90px;
  margin: 0 30px;
}
header .header-wrapper .nav-container {
  flex-grow: 2;
  display: flex;
  justify-content: center;
}
header .header-wrapper .nav-container nav {
  display: flex;
  align-items: center;
}
header .header-wrapper .nav-container nav ul {
  list-style-type: none;
  display: flex;
}
header .header-wrapper .nav-container nav ul li {
  margin: 0 5px;
}
header .header-wrapper .nav-container nav ul li a {
  font-family: "Inria Sans", sans-serif;
  font-weight: 400;
  font-size: 20px;
  color: #2CA6AE;
  padding: 10px 5px;
}
header .header-wrapper .nav-container nav ul li a:hover {
  color: red;
}
header .contact-btn {
  width: 250px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}
header .contact-btn a {
  display: flex;
  width: 200px;
  height: 50px;
  border: solid 2px #2CA6AE;
  justify-content: center;
  align-items: center;
  border-radius: 30px;
  font-family: "Baloo 2", sans-serif;
  font-weight: 700;
  font-size: 20px;
  color: #2CA6AE;
}
header .contact-btn a:hover {
  color: red;
}

.hero {
  height: 100vh;
  margin-top: -120px;
  width: 100%;
  position: relative;
  overflow: hidden;
}
.hero .slides {
  display: flex;
  width: 100%;
  height: 100%;
  position: relative;
  left: 0;
}
.hero .slides .slide {
  display: flex;
  width: 100%;
  height: 100%;
  flex-shrink: 0;
}
.hero .slides .slide img {
  object-fit: cover;
  object-position: top;
  width: 100%;
}
.hero .slides .slide .slide-overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.hero .slides .slide .slide-overlay .titles {
  margin: 0 20px;
  margin-top: 180px;
  width: 70%;
}
.hero .slides .slide .slide-overlay .titles .sub-title {
  font-family: "Baloo 2", sans-serif;
  font-weight: 600;
  text-transform: uppercase;
  color: #ffffff;
  font-size: 20px;
  text-align: center;
}
.hero .slides .slide .slide-overlay .titles .title {
  font-family: "Baloo 2", sans-serif;
  font-weight: 700;
  color: #ffffff;
  font-size: 36px;
  text-align: center;
}
.hero .slides .slide .slide-overlay .buttons {
  margin-top: 50px;
  display: flex;
  gap: 24px;
}
.hero .slides .slide .slide-overlay .buttons .button {
  width: 200px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 30px;
  color: #ffffff;
  font-family: "Baloo 2", sans-serif;
  font-weight: 700;
  font-size: 20px;
}
.hero .slides .slide .slide-overlay .buttons .button.left {
  background: #FDB817;
}
.hero .slides .slide .slide-overlay .buttons .button.right {
  background: #3CCCD5;
}
.hero .slides .social {
  position: absolute;
  right: 30px;
  bottom: 30px;
  height: 64px;
  width: 300px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #ffffff;
  border-radius: 50px;
  padding: 0 15px;
}
.hero .slides .social img {
  height: 30px;
}
.hero .slides .social a {
  font-family: "Inria Sans", sans-serif;
  font-weight: 400;
  font-size: 15px;
  color: #2CA6AE;
}
.hero .bars {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.hero .bars .bar {
  height: 15px;
  width: 15px;
  border-radius: 50%;
  background-color: #ffffff;
}
.hero .bars .bar.active {
  background-color: #2CA6AE;
}